.call-log {
  height: 100%;
}
.call-log .log-panel {
  height: calc(100% - 40px);
  display: flex;
  flex-direction: row;
}
.call-log .log-panel .left {
  flex: 1;
  height: 100%;
  overflow-y: auto;
}
.call-log .log-panel .left ul {
  margin: 0;
  padding: 0;
}
.call-log .log-panel .left li {
  margin: 0;
  padding: 0;
  list-style-type: none;
  border-bottom: 1px solid #eee;
}
.call-log .log-panel .left .rec {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.call-log .log-panel .left .rec.selected {
  background-color: #03a9f5;
}
.call-log .log-panel .left .rec:hover {
  cursor: pointer;
  background-color: #03a9f5;
  color: #fff;
}
.call-log .log-panel .left .rec i {
  display: block;
  width: 40px;
  height: 40px;
  background-repeat: no-repeat;
  background-size: contain;
}
.call-log .log-panel .left .rec i.no-answer {
  background-image: url(/images/ed601a909dc43d7b7635db4c65eb80ce.png);
}
.call-log .log-panel .left .rec i.inbound {
  background-image: url(/images/52fb285bece12f318504cacf8be37de2.png);
}
.call-log .log-panel .left .rec i.outbound {
  background-image: url(/images/e5ee54abbc1ce33aeff238752faae551.png);
}
.call-log .log-panel .left .rec .info {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 0 20px;
}
.call-log .log-panel .left .rec time {
  font-size: 1.2rem;
}
.call-log .log-panel .right {
  flex: 1;
  height: 100%;
  overflow-y: auto;
  background-color: #f5f5f5;
}
.call-log .log-panel .right .selected-log {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 10px;
}
.call-log .log-panel .right .selected-log .name {
  padding: 10px;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.call-log .log-panel .right .selected-log .name .avatar {
  display: block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #cccccc;
  margin: 0 10px;
}
.call-log .log-panel .right .selected-log .name span {
  font-size: 1.6rem;
}
.call-log .log-panel .right .selected-log .no {
  margin-top: 10px;
  padding: 20px 90px;
  background-color: #fff;
  background-image: url(/images/d8e62734145c92e59439aba2f77523cf.png);
  background-repeat: no-repeat;
  background-size: 50px 50px;
  background-position: 27px 5px;
}
.call-log .log-panel .right .selected-log .list {
  height: calc(100% - 161px);
  margin-top: 10px;
  padding: 10px;
  background-color: #fff;
  overflow-y: auto;
}
.call-log .log-panel .right .selected-log .list ul {
  margin: 0;
  padding: 0;
}
.call-log .log-panel .right .selected-log .list li {
  margin: 0;
  padding: 5px 10px;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
