.left-nav {
  font-size: 14px;
  height: 100%;
  width: 110px;
  background-color: #0a7cef;
  overflow: hidden;
}
.left-nav ul {
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.left-nav li {
  margin: 0;
  padding: 0;
  list-style-type: none;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.left-nav li div.logo {
  width: 78px;
  height: 78px;
  background-image: url(/images/ab109cd5b027dbd1d3226ebad576a987.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  margin: 20px auto 20px auto;
}
.left-nav li a {
  display: block;
  width: 100%;
  overflow: hidden;
  text-align: center;
}
.left-nav li a span {
  color: #fff;
  font-size: 1.4rem;
}
.left-nav li a i {
  display: block;
  background-size: contain;
  margin: 20px auto;
}
.left-nav li a.home.active {
  background-color: #24a0f6;
}
.left-nav li a.home i {
  width: 65px;
  height: 103px;
  background-image: url(/images/a54710574fabd9b83779063263625cfc.png);
  background-repeat: no-repeat;
}
.left-nav li a.collection.active {
  background-color: #24a0f6;
}
.left-nav li a.collection i {
  width: 72px;
  height: 104px;
  background-image: url(/images/ac644935507a28c4feee14c3bed9e96e.png);
  background-repeat: no-repeat;
}
.left-nav li a.toolkit.active {
  background-color: #24a0f6;
}
.left-nav li a.toolkit i {
  width: 77px;
  height: 87px;
  background-image: url(/images/d4d894426a25e34c13e7eae4a3d46e22.png);
  background-repeat: no-repeat;
}
.left-nav li a.setting.active {
  background-color: #24a0f6;
}
.left-nav li a.setting i {
  width: 75px;
  height: 75px;
  background-image: url(/images/1785975a4e54851718dc87e1ecd376d0.png);
  background-repeat: no-repeat;
}
