.user-detail {
  height: 100%;
}
.user-detail .info {
  background-color: #f9f9f9;
  height: calc(100% - 40px);
  overflow-y: auto;
}
.user-detail .info ul {
  display: block;
  margin: 50px;
  padding: 0;
  background-color: #fff;
}
.user-detail .info li {
  margin: 0;
  padding: 10px 20px;
  list-style-type: none;
  border-bottom: 1px solid #eee;
  display: flex;
  flex-direction: column;
}
.user-detail .info li label {
  color: #a1a1a1;
  font-size: 1.2rem;
}
.user-detail .info li span {
  color: #222;
  font-size: 1.4rem;
}
.user-detail .info li:last-child {
  border-bottom: none;
}
