.widget-phone-info {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.widget-phone-info .connecting {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.widget-phone-info .connecting .info {
  flex: 1;
  font-size: 16px;
  display: flex;
  align-items: center;
}
.widget-phone-info .connecting .lstatus {
  flex: 1;
  font-size: 48px;
  color: #3599fd;
}
.widget-phone-info .connected {
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.widget-phone-info .connected .progress {
  position: absolute;
  bottom: 0px;
  right: 0px;
  width: 100%;
  height: 10px;
  background-image: url(/images/28bfc756b6e98fcd30d77a41b1ace504.gif);
  background-repeat: no-repeat;
  background-size: cover;
}
.widget-phone-info .connected .img {
  flex: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.widget-phone-info .connected .img .title {
  color: #2fce74;
  font-size: 14px;
  padding: 10px 0;
}
.widget-phone-info .connected .img .phone-type {
  width: 75px;
  height: 150px;
  margin: 0 auto;
}
.widget-phone-info .connected .details {
  flex: 3;
  display: flex;
  flex-direction: column;
}
.widget-phone-info .connected .details .mark {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-bottom: 10px;
}
.widget-phone-info .connected .details .mark .dt {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
}
.widget-phone-info .connected .details .case-data label:after {
  content: "";
}
.widget-phone-info .connected .details .case-data span {
  font-weight: bold;
  color: #3599fd;
}
.widget-phone-info .connected .details .btn {
  padding: 10px 0;
}
.widget-phone-info .connected .details .btn button {
  margin-right: 5px;
}
