.wechat-panel {
  height: 100%;
  display: flex;
  flex-direction: row;
}
.wechat-panel .opt-list {
  height: 100%;
  width: 200px;
  overflow-y: auto;
  border-right: 1px solid #eee;
}
.wechat-panel .opt-list ul {
  margin: 0;
  padding: 0;
}
.wechat-panel .opt-list li:hover {
  background-color: #e1f4fd;
}
.wechat-panel .opt-list li a {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin: 0;
  padding: 10px;
  list-style-type: none;
  border-bottom: 1px solid #eee;
  font-size: 1.2rem;
}
.wechat-panel .opt-list li a i {
  display: block;
  width: 30px;
  height: 30px;
  background-repeat: no-repeat;
  background-size: contain;
}
.wechat-panel .opt-list li a i.account {
  background-image: url(/images/14f1725388a8e611274fbd410b34b743.png);
}
.wechat-panel .opt-list li a i.chats {
  background-image: url(/images/898523de17d9e96b2d7f15f67dab884b.png);
}
.wechat-panel .opt-list li a i.friends {
  background-image: url(/images/57cc7ef1a0e77919b08828bb0ec6783b.png);
}
.wechat-panel .opt-list li a i.group {
  background-image: url(/images/84b007901e8758e182b6b437c0d72f20.png);
}
.wechat-panel .opt-list li a i.dynamic {
  background-image: url(/images/2b65573644d0b4f2c45705d9ccd0c644.png);
}
.wechat-panel .opt-list li a i.mini-program {
  background-image: url(/images/ab3bfa6a2806d5ee6ecc8dbb8884a2b9.png);
}
.wechat-panel .opt-list li a i.favorite {
  background-image: url(/images/e70498349b82bb4309c458b60e86174f.png);
}
.wechat-panel .opt-list li a i.bind {
  background-image: url(/images/d62a637e6afa98e3c6d181d81c04b9e0.png);
}
.wechat-panel .opt-list li a i.search {
  background-image: url(/images/acda7f4881f3b132446611a9495570ff.png);
}
.wechat-panel .opt-list li a .txt {
  display: flex;
  flex-direction: column;
  color: #33b9f7;
  padding-left: 10px;
}
.wechat-panel .opt-list li a .txt .black {
  color: #222;
}
.wechat-panel .opt-list li a .txt .gray {
  color: #b3b3b3;
}
.wechat-panel .opt-list li a .txt strong {
  font-style: normal;
  font-weight: normal;
  color: #f00;
}
.wechat-panel .opt-container {
  height: 100%;
  flex: 1;
}
