.collecting {
  height: 100%;
}
.collecting .header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-self: center;
}
.collecting .header .phone {
  flex: 1;
}
.collecting .header .phone .iphone {
  width: 65px;
  height: 130px;
  background-image: url(/images/83dd6380faca2ca4dac07e2407fa6d7e.png);
  background-repeat: no-repeat;
  background-size: contain;
  margin: 0 auto;
}
.collecting .header .phone .android {
  width: 65px;
  height: 131px;
  background-image: url(/images/818d4e40e081658dfd162ce055c322d4.png);
  background-repeat: no-repeat;
  background-size: contain;
  margin: 0 auto;
}
.collecting .header .info {
  flex: 3;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.collecting .header .info div {
  padding: 5px 0;
}
.collecting .header .info .phone-info {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.collecting .header .btn {
  flex: 1;
  display: flex;
  justify-content: center;
  align-self: center;
}
.collecting .header .btn .ant-btn {
  margin: 0 4px;
}
.collecting .process {
  margin: 0 auto;
}
.collecting .tools-panel {
  display: flex;
}
.collecting .bar {
  background-image: linear-gradient(to right, #1a86ea, #fff);
  padding: 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.collecting .bar span {
  color: #fff;
}
.collecting .bar a {
  color: #02aaf5;
}
.collecting .app-panel {
  width: 100%;
  height: calc(100% - 152px);
  padding: 10px;
  overflow-y: auto;
}
