.app-list .bar {
  background-image: linear-gradient(to right, #1a86ea, #fff);
  padding: 0 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.app-list .bar span {
  color: #fff;
}
.app-list .bar a {
  color: #02aaf5;
}
.app-list .category {
  width: 100%;
  height: calc(100% - 152px);
  padding: 10px;
  overflow-y: auto;
}
.app-list .category .box {
  width: auto;
  padding: 10px 0;
}
.app-list .category .item {
  cursor: pointer;
  position: relative;
  display: block;
  width: 150px;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f9f9f9;
  border: 1px solid #eeeeee;
  border-radius: 14px;
}
.app-list .category .item .mask {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(149, 149, 149, 0.8);
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 14px;
  font-size: 1.2rem;
}
.app-list .category .item .selected {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: 14px;
  background-image: url(/images/2efb2e32cc453b1ab881d61ee168b692.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.app-list .category .item .txt {
  line-height: 2.8rem;
}
@media only screen and (min-width: 1200px) {
  .app-list .category .item {
    width: 130px;
    height: 130px;
  }
}
@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .app-list .category .item {
    width: 90px;
    height: 90px;
  }
  .app-list .category .item .app-icon {
    width: 50px;
    height: 50px;
  }
}
@media only screen and (max-width: 992px) {
  .app-list .category .item {
    width: 75px;
    height: 75px;
  }
  .app-list .category .item .app-icon {
    width: 45px;
    height: 45px;
  }
  .app-list .category .item .txt {
    line-height: 2.4rem;
    font-size: 1.2rem;
  }
}
