.unit-root {
  height: 100%;
}
.unit-root .table-panel {
  height: calc(100% - 40px);
  overflow-y: auto;
}
.unit-root .table-panel .info-bar {
  padding: 10px;
  background-color: #1890ff;
}
.unit-root .table-panel .info-bar label {
  color: #fff;
  padding: 5px;
}
.unit-root .table-panel .info-bar em {
  font-weight: bold;
  color: #fff;
}
.unit-root .table-panel .condition-bar {
  padding: 5px;
  border-bottom: 1px solid #e8e8e8;
}
