.setting-panel {
  height: 100%;
  display: flex;
  flex-direction: row;
}
.setting-panel .setting-menu {
  flex: 1;
  height: 100%;
  margin: 0;
  padding: 0;
  border-right: 1px solid #eee;
}
.setting-panel .setting-menu ul {
  margin: 0;
  padding: 0;
}
.setting-panel .setting-menu li {
  margin: 0;
  padding: 0;
  list-style-type: none;
  border-bottom: 1px solid #eeeeee;
}
.setting-panel .setting-menu li a {
  display: block;
  margin: 0;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  color: #666666;
}
.setting-panel .setting-menu li a.active {
  background-color: #d7e7f0;
}
.setting-panel .setting-menu li a span {
  margin-left: 10px;
}
.setting-panel .setting-menu li .collection i {
  display: block;
  width: 40px;
  height: 40px;
  background-image: url(/images/38dcf3d1337b3df8fc88b01b5f5da5f6.png);
  background-repeat: no-repeat;
  background-size: contain;
}
