.officer-panel {
  height: 100%;
}
.officer-panel .police-list {
  height: calc(100% - 40px);
  overflow-y: auto;
}
.officer-panel .police-list ul {
  margin: 0;
  padding: 0;
}
.officer-panel .police-list li {
  margin: 5px;
  padding: 0;
  list-style-type: none;
  display: inline-block;
}
.officer-panel .police-list .police {
  position: relative;
  cursor: pointer;
  border: 1px solid #eee;
  border-radius: 5px;
  background-color: #f9f9f9;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.officer-panel .police-list .police .avatar {
  display: block;
  width: 50px;
  height: 81px;
  background-image: url(/images/cc491059e59da165a41df10745c5faba.png);
  background-repeat: no-repeat;
  background-size: contain;
}
.officer-panel .police-list .police .info {
  display: flex;
  flex-direction: column;
  padding: 10px;
}
.officer-panel .police-list .police .info span {
  display: block;
  max-width: 120px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 5px;
  font-size: 1.4rem;
  color: #222;
}
.officer-panel .police-list .police .info em {
  display: block;
  padding: 5px;
  font-size: 1.2rem;
  font-style: normal;
}
.officer-panel .police-list .police .drop {
  position: absolute;
  top: 0;
  right: 0;
  color: #a61614;
}
.officer-panel .police-list .police .drop:hover {
  color: #fe2c2f;
}
