.tools-menu {
  height: 100%;
}
.tools-menu menu {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
}
.tools-menu li {
  flex: 1;
  display: inline-block;
  margin: 10px;
  padding: 15px;
  border-radius: 5px;
  list-style-type: none;
  border: 1px solid #eee;
  background-color: #f9f9f9;
}
.tools-menu li a {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.tools-menu li .info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-left: 15px;
}
.tools-menu li .info span {
  font-size: 1.6rem;
  color: #222;
}
.tools-menu li .info em {
  font-size: 1.2rem;
  color: #989696;
  font-style: normal;
}
.tools-menu i {
  display: block;
  width: 80px;
  height: 80px;
  background-repeat: no-repeat;
  background-size: contain;
}
@media screen and (min-width: 1200px) {
  .tools-menu i {
    width: 80px;
    height: 80px;
  }
}
@media screen and (min-width: 992px) and (max-width: 1200px) {
  .tools-menu i {
    width: 60px;
    height: 60px;
  }
}
@media screen and (max-width: 992px) {
  .tools-menu i {
    width: 40px;
    height: 40px;
  }
}
.tools-menu i.gen {
  background-image: url(/images/ab90ca7e81cbb99d47486ef9130c6601.png);
}
.tools-menu i.upload {
  background-image: url(/images/647d6dcf2474f225fa771c6623601110.png);
}
.tools-menu i.report {
  background-image: url(/images/cf0f76af56f2af6690418076208ff0c0.png);
}
