.dynamic-panel {
  height: 100%;
}
.dynamic-panel .msg-list {
  height: calc(100% - 40px);
  overflow-y: auto;
  background-color: #f5f5f5;
}
.dynamic-panel .msg-list ul,
.dynamic-panel .msg-list li {
  margin: 0;
  padding: 0;
}
.dynamic-panel .msg-list li {
  list-style-type: none;
  padding: 10px 40px;
  border-bottom: 1px solid #eee;
}
.dynamic-panel .msg-list li div {
  font-size: 1.2rem;
  padding: 10px 0;
}
.dynamic-panel .msg-list li div.name {
  font-size: 1.4rem;
  color: #1890ff;
}
.dynamic-panel .msg-list li div.images {
  display: flex;
  flex-direction: row;
}
.dynamic-panel .msg-list li div.images img {
  display: inline-block;
  width: 80px;
  height: 80px;
  border: 1px solid #eee;
  margin: 5px;
}
