.base-detail {
  height: 100%;
}
.base-detail .bd-panel {
  height: calc(100% - 40px);
  position: relative;
  background-color: #709efc;
}
.base-detail .bd-panel .center-panel {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  position: absolute;
  top: 80px;
  left: 50px;
  bottom: 50px;
  right: 80px;
}
.base-detail .bd-panel .center-panel div:first-child {
  flex: 2;
  height: 100%;
  border-right: 6px solid #eeeeee;
  display: flex;
  justify-items: center;
  align-items: center;
}
.base-detail .bd-panel .center-panel div:last-child {
  flex: 3;
  height: 100%;
  display: flex;
  justify-items: center;
  align-items: center;
}
.base-detail .bd-panel .center-panel .phone-table {
  margin: 0 auto;
  width: 80%;
  color: #222;
}
.base-detail .bd-panel .center-panel .phone-table tr td {
  padding: 5px 10px;
}
.base-detail .bd-panel .center-panel .phone-table tr td:first-child {
  text-align: right;
}
.base-detail .bd-panel .center-panel .phone-table tr td:first-child:after {
  content: "";
}
.base-detail i {
  display: block;
  margin: 0 auto;
  width: 200px;
  height: 430px;
  background-repeat: no-repeat;
  background-size: contain;
}
.base-detail i.iphone {
  background-image: url(/images/d2a523a04149e48eb31701b76876358d.png);
}
.base-detail i.android {
  background-image: url(/images/fa1d891d449c26e8ae48e16d44636cc1.png);
}
.base-detail i.phone {
  background-image: url(/images/1858ec61778b663835f7c2b189535f98.png);
}
