.case-add-panel {
  height: 100%;
}
.case-add-panel .form-panel {
  height: calc(100% - 40px);
  display: flex;
  align-items: flex-start;
  overflow-y: auto;
}
.case-add-panel .form-panel .ant-form {
  width: 100%;
}
.case-add-panel .form-panel .app-list-item > div {
  width: 100%;
}
.case-add-panel .form-panel .app-list-item .app-list-panel {
  display: none;
  width: 100%;
}
