.init {
  width: 100%;
  height: 100%;
  position: relative;
}
.init .bg {
  width: 100%;
  height: 172px;
  background-image: url(/images/968363643d3d58356d456389d604ad2c.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.init .panel {
  position: absolute;
  top: 50px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  border: 1px solid #efefef;
  background-color: #fff;
  box-shadow: 1px 1px 1px #efefef;
  display: flex;
  flex-direction: column;
}
.init .panel .row {
  width: auto;
  box-sizing: border-box;
  flex: 1;
  display: flex;
  flex-direction: row;
}
.init .panel .row .col {
  flex: 1;
  box-sizing: border-box;
  display: inline-block;
  padding: 5px;
}
.init .panel .cell {
  display: flex;
  flex-direction: column;
  height: 100%;
  border: 1px solid #efefef;
}
.init .panel .cell .no {
  background-color: #0a7cef;
  color: #fff;
  height: 30px;
  line-height: 30px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.init .panel .cell .no span {
  padding: 0 5px;
}
.init .panel .cell .place {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}
