.phonebook {
  height: 100%;
}
.phonebook .book-panel {
  height: calc(100% - 40px);
  display: flex;
  flex-direction: row;
}
.phonebook .book-panel .left {
  flex: 1;
  height: 100%;
  overflow-y: auto;
}
.phonebook .book-panel .left ul {
  margin: 0;
  padding: 0;
}
.phonebook .book-panel .left li {
  margin: 0;
  padding: 0;
  list-style-type: none;
  border-bottom: 1px solid #eee;
}
.phonebook .book-panel .left .rec {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 10px 20px;
}
.phonebook .book-panel .left .rec.selected {
  color: #fff;
  background-color: #03a9f5;
}
.phonebook .book-panel .left .rec:hover {
  cursor: pointer;
  background-color: #03a9f5;
  color: #fff;
}
.phonebook .book-panel .right {
  flex: 1;
  height: 100%;
  overflow-y: auto;
  background-color: #f5f5f5;
}
.phonebook .book-panel .right .selected-log {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 10px;
}
.phonebook .book-panel .right .selected-log .name {
  padding: 10px;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.phonebook .book-panel .right .selected-log .name .avatar {
  display: block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #cccccc;
  margin: 0 10px;
}
.phonebook .book-panel .right .selected-log .name span {
  font-size: 1.6rem;
}
.phonebook .book-panel .right .selected-log .no {
  margin-top: 10px;
  padding: 20px;
  background-color: #fff;
}
.phonebook .book-panel .right .selected-log .no span:first-child:after {
  content: "";
}
