.case-info {
  height: 100%;
}
.case-info .tips {
  padding: 10px;
}
.case-info .tips-item {
  width: auto;
  height: 110px;
  padding: 5px;
  border: 1px solid #eeeeee;
  border-radius: 10px;
  background-color: #f9f9f9;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.case-info .tips-item .ico {
  flex: 2;
}
.case-info .tips-item .ico i {
  display: block;
  margin: 0 auto;
  background-repeat: no-repeat;
  background-size: contain;
}
@media only screen and (min-width: 1200px) {
  .case-info .tips-item .ico i.iphone {
    width: 50px;
    height: 100px;
    background-image: url(/images/83dd6380faca2ca4dac07e2407fa6d7e.png);
  }
  .case-info .tips-item .ico i.phone {
    width: 50px;
    height: 100px;
    background-image: url(/images/818d4e40e081658dfd162ce055c322d4.png);
  }
  .case-info .tips-item .ico i.statistical {
    width: 72px;
    height: 72px;
    background-image: url(/images/a72a85d46c6b7f87601f29ac3aa173e0.png);
  }
  .case-info .tips-item .ico i.timer {
    width: 72px;
    height: 72px;
    background-image: url(/images/af29ba14521878773f320c69d13c888d.png);
  }
  .case-info .tips-item .ico i.case {
    width: 84px;
    height: 67px;
    background-image: url(/images/039d0255d7fe5ca68193eb4299ceed52.png);
  }
}
@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .case-info .tips-item .ico i.iphone {
    width: 33px;
    height: 64px;
    background-image: url(/images/83dd6380faca2ca4dac07e2407fa6d7e.png);
  }
  .case-info .tips-item .ico i.phone {
    width: 33px;
    height: 64px;
    background-image: url(/images/818d4e40e081658dfd162ce055c322d4.png);
  }
  .case-info .tips-item .ico i.statistical {
    width: 56px;
    height: 56px;
    background-image: url(/images/a72a85d46c6b7f87601f29ac3aa173e0.png);
  }
  .case-info .tips-item .ico i.timer {
    width: 56px;
    height: 56px;
    background-image: url(/images/af29ba14521878773f320c69d13c888d.png);
  }
  .case-info .tips-item .ico i.case {
    width: 63px;
    height: 50px;
    background-image: url(/images/039d0255d7fe5ca68193eb4299ceed52.png);
  }
}
@media screen and (max-width: 992px) {
  .case-info .tips-item .ico i.iphone {
    width: 33px;
    height: 64px;
    background-image: url(/images/83dd6380faca2ca4dac07e2407fa6d7e.png);
  }
  .case-info .tips-item .ico i.phone {
    width: 33px;
    height: 64px;
    background-image: url(/images/818d4e40e081658dfd162ce055c322d4.png);
  }
  .case-info .tips-item .ico i.statistical {
    width: 56px;
    height: 56px;
    background-image: url(/images/a72a85d46c6b7f87601f29ac3aa173e0.png);
  }
  .case-info .tips-item .ico i.timer {
    width: 56px;
    height: 56px;
    background-image: url(/images/af29ba14521878773f320c69d13c888d.png);
  }
  .case-info .tips-item .ico i.case {
    width: 63px;
    height: 50px;
    background-image: url(/images/039d0255d7fe5ca68193eb4299ceed52.png);
  }
}
.case-info .tips-item .info {
  flex: 3;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  font-size: 1.6rem;
}
@media only screen and (min-width: 1200px) {
  .case-info .tips-item .info {
    font-size: 1.6rem;
  }
}
@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .case-info .tips-item .info {
    font-size: 1.6rem;
  }
}
@media only screen and (max-width: 992px) {
  .case-info .tips-item .info {
    font-size: 1.4rem;
  }
}
.case-info .tips-item .info div {
  padding: 5px 0;
}
.case-info .tips-item .info a {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media only screen and (min-width: 1200px) {
  .case-info .tips-item .info a {
    width: 140px;
  }
}
@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .case-info .tips-item .info a {
    width: 110px;
  }
}
@media only screen and (max-width: 992px) {
  .case-info .tips-item .info a {
    width: 65px;
  }
}
.case-info .bar {
  background-image: linear-gradient(to right, #1a86ea, #fff);
  padding: 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.case-info .bar span {
  color: #fff;
}
.case-info .bar a {
  color: #02aaf5;
}
.case-info .category {
  width: 100%;
  height: calc(100% - 170px);
  padding: 10px;
  overflow-y: auto;
}
.case-info .category .box {
  width: auto;
  padding: 10px 0;
}
.case-info .category .item {
  cursor: pointer;
  position: relative;
  display: block;
  width: 150px;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f9f9f9;
  border: 1px solid #eeeeee;
  border-radius: 14px;
}
.case-info .category .item a {
  font-size: 1.2rem;
}
.case-info .category .item .mask {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(149, 149, 149, 0.8);
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 14px;
  font-size: 1.2rem;
}
.case-info .category .item .selected {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: 14px;
  background-image: url(/images/2efb2e32cc453b1ab881d61ee168b692.png);
  background-repeat: no-repeat;
  background-size: contain;
}
@media only screen and (min-width: 1200px) {
  .case-info .category .item {
    width: 150px;
    height: 150px;
  }
}
@media only screen and (min-width: 992px) and (max-width: 1200px) {
  .case-info .category .item {
    width: 120px;
    height: 120px;
  }
}
@media only screen and (max-width: 992px) {
  .case-info .category .item {
    width: 105px;
    height: 105px;
  }
}
